#navbar{
    top: 0px;
    position: fixed;

    display: flex;

    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 25px;

    background-color: rgb(0, 0, 0);
    color: white;

    box-sizing: border-box;
    padding: 25px;
}

#navbar section{
    display: flex;
    justify-content: center;
    gap: 25px;
}

#navbar section a{
    color: white;
    text-decoration: none;
}